@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrapper{width:288px;margin:0 auto}@media (min-width: 768px) and (max-width: 1200px){.wrapper{width:700px}}@media (min-width: 1200px){.wrapper{width:1200px}}.main-container{background-color:#fff;background-image:linear-gradient(to bottom right, #fff 50%, #FFF3EB 50%);height:100vh}.container{display:flex;flex-direction:column;height:100vh;justify-content:center;align-items:center}@media (min-width: 768px){.container{flex-direction:row}}.container__logo img{width:100%}@media (min-width: 768px) and (max-width: 1200px){.container__logo{width:60%}}@media (min-width: 1200px){.container__logo{width:624px}}.container__startGame{display:flex;flex-direction:column;height:30%;width:100%;justify-content:space-evenly}@media (min-width: 768px){.container__startGame{width:40%}}.title{font-family:'Inter', sans-serif;font-style:normal;font-weight:600;font-size:32px;color:#1C1C21;text-align:center}@media (min-width: 768px){.title{text-align:start;font-size:34px}}@media (min-width: 1200px){.title{font-size:56px}}

.btn{background:#FF8B37;text-decoration:none;border:transparent;border-radius:8px;color:#fff;padding:16px 24px;font-family:'Inter', sans-serif;font-style:normal;font-weight:600;font-size:14px;text-align:center}.btn:hover{background:#FFAC70}.btn:active{background:#E87928}@media (min-width: 1200px){.btn{width:296px}}

.gameContainer{font-family:'Inter', sans-serif;background:#E5E5E5;height:100vh}@media (min-width: 900px){.gameContainer{display:flex}}.gameContainer__title{font-style:normal;font-weight:600;font-size:18px;text-align:center}.gameContainer__main{display:flex;flex-direction:column;justify-content:space-evenly;height:100vh}.gameContainer__answerBlock{width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center}@media (min-width: 768px){.gameContainer__answerBlock{flex-direction:row;flex-wrap:wrap;justify-content:center;align-items:center}}@media (min-width: 1650px){.gameContainer__answerBlock{justify-content:flex-end}}.amount-block{width:30%;background:#fff;display:flex;flex-direction:column;justify-content:center;align-items:center}@media (max-width: 900px){.amount-block{display:none}}@media (min-width: 1024px){.amount-block{width:315px}}@media (min-width: 1650px){.amount-block{width:20%}}

.answer{position:relative;width:100%;height:56px;display:flex;align-items:center;margin-bottom:8px;background-repeat:no-repeat;cursor:pointer}@media (min-width: 768px){.answer{width:45%}}@media (min-width: 900px){.answer{width:48%}}@media (min-width: 1200px){.answer{width:389px}}@media (min-width: 1650px){.answer{width:500px}}.answer:before{content:'';display:inline-block;height:1px;width:15px;background:#D0D0D8;position:absolute;left:-7px;top:50%;transform:translate(-50%, -50%)}@media (min-width: 1024px){.answer:before{left:-12px;width:25px}}@media (min-width: 1200px){.answer:before{left:-25px;width:52px}}@media (min-width: 1650px){.answer:before{left:-25px;width:52px}}.answer:after{content:'';display:inline-block;height:1px;width:15px;background:#D0D0D8;position:absolute;right:-21px;top:50%;transform:translate(-50%, -50%)}@media (min-width: 768px){.answer:after{right:5px}}@media (min-width: 1024px){.answer:after{right:12px;width:25px}}@media (min-width: 1200px){.answer:after{right:25px;width:52px}}@media (min-width: 1650px){.answer:after{right:135px;width:52px}}.answer__variant{display:inline-block;font-style:normal;font-weight:600;font-size:14px;color:#FF8B37;margin:0 10px 0 25px}.answer__text{font-family:'Inter', sans-serif;font-style:normal;font-weight:normal;font-size:14px}

.bm-burger-button{position:fixed;width:16px;height:14px;right:21px;top:20px}@media (min-width: 900px){.bm-burger-button{display:none}}.bm-burger-bars{background:#373a47}.bm-burger-bars-hover{background:#a90000}.bm-cross-button{height:14px;width:14px}.bm-cross{background:#000}.bm-menu-wrap{position:fixed;height:100%}.bm-menu{background:#E5E5E5}.bm-morph-shape{fill:#373a47}.bm-item-list{color:#b8b7ad}.bm-item{display:flex !important}.menuContainer{display:flex;flex-direction:column;justify-content:center;align-items:center;margin-top:50px;outline:none}

.amount{position:relative;display:flex;justify-content:center;align-items:center;height:32px;margin-bottom:8px;background-repeat:no-repeat;width:240px;z-index:1}.amount:before{content:'';display:inline-block;height:1px;width:40px;background:#D0D0D8;position:absolute;left:-17px;top:50%;transform:translate(-50%, -50%);z-index:0}.amount:after{content:'';display:inline-block;height:1px;width:40px;background:#D0D0D8;position:absolute;right:-57px;top:50%;transform:translate(-50%, -50%)}.amount__text{font-family:'Inter', sans-serif;font-style:normal;font-weight:normal;font-size:14px}.current:after{background:#FF8B37}.current:before{background:#FF8B37}

.container__startGame__score{font-style:normal;font-weight:600;font-size:18px;color:#1C1C21;opacity:0.5;text-align:center}.main{background-color:#fff;height:100vh}

.content-notFound{width:100%;display:flex;flex-direction:column;align-items:center;justify-content:center}.content-notFound__title{font-size:60px}.content-notFound__description{padding:20px}

